import VisualComponent from "jx/core/comps/VisualComponent";
let _template = `
    <div class="form-group input-group  input-sm">
        <span class="input-group-addon input-sm"><span class="fa fa-search"></span></span>
        <input type="search" class="form-control input-sm" id="value" placeholder="Search">
    </div>
`;
export default class SearchBox extends VisualComponent {
    constructor(dataObject) {
        super(dataObject);
        this.controller = this.dataObject.controller;
        this.addEventDispatcher("onchanged");
        var item = $(_template);
        item.prop("id", this.data.id);
        item.on("search", (event) => {
            //this.controller.onChangeData(event); 
            this.onchanged.dispatch({ target: this });
        });
        if (this.data.onchanged) {
            this.onchanged.add(this.data.onchanged);
        }
        ;
        item.data("controller", this);
        this.item = item;
        this.view = this.item;
        this.value = this.data.value || "";
    }
    get value() {
        //console.log('GETVALUE', this.item[0].value)
        return this.view.find("#value")[0].value;
    }
    set value(val) {
        this.view.find("#value")[0].value = val;
    }
}
